import React, {useRef} from 'react'
import '../componentCSS/Form.css'

export default function LoadingScreen(props){
    const usernameRef = useRef()
    const passwordRef = useRef()

    const submitAction = ()=>{
        let userNameInput = usernameRef.current.value;
        let passWordInput = passwordRef.current.value;
        if(userNameInput.length <= 0){
            alert('Please enter a username')
            usernameRef.focus()
        }
        else if(passWordInput.length <= 0){
            alert('Please enter password')
            passwordRef.focus()
        }
        else
            props.changeLoading(true)
    }

    return <div className="Form">
        <h3>Log-in</h3>
        <div className="inputFields flex">
            <label>Username</label>
            <input ref={usernameRef} type='text'></input>
        </div>
        <div className="inputFields flex">
            <label>Password</label>
            <input ref={passwordRef} type='password'></input>
        </div>
        <div className='flex justifyCenter'>
            <button onClick={submitAction} className='connectButton flex justifyCenter alignCenter'>
                <svg viewBox='0 0 110 110'>
                    <circle cx='50' cy='50' r='50' />
                </svg>
                CONNECT
            </button>
        </div>
    </div>
}