import '../componentCSS/LoadingScreen.css'
import { Dot } from 'react-animated-dots';

export default function LoadingScreen(){
    return <div className="LoadingScreen flex justifyCenter alignCenter">
        <div className='LoadingStatus' style={{transform: "translateY(-5vh)"}}>
        <svg viewBox='40 40 75 75'>
            <circle className='SignalBars' cx='55' cy='55' r='50' />
            <circle className='SignalBars' cx='55' cy='55' r='35' />
            <circle className='SignalBars' cx='55' cy='55' r='20' />
            <circle className='SignalBars' cx='55' cy='55' r='2.5' />
        </svg>
        <h1 className='StatusTitle'>Connecting<Dot>.</Dot><Dot>.</Dot><Dot>.</Dot></h1>
        </div>
    </div>
}